import * as React from "react";
import Layout from "../components/common/Layout/Layout";
import ArticleCardItem, {
  CardSize,
} from "../components/common/ArticleCardItem/ArticleCardItem";
import { NewsPageHeader } from "../components/news";
import { graphql, PageProps } from "gatsby";
import { mapArticleNodeToArticlePreviewInfo } from "../mappers";
import {
  DesktopCardsContainer,
  MediumCards,
  MobileCardsContainer,
  NewsContentContainer,
  NewsList,
  SmallCards,
} from "../components/news/styled";
import { useLunr } from "react-lunr";
import { ArticlePreviewInfo } from "../types";
import { GatsbySeoComponent, MetaData, TopLevelPageJSONLDSchemas } from "../components/common/SEO";
import { NoResultsText } from "../components/common/styled";

const NewsPage = ({ data }: PageProps<Queries.NewsPageQuery>) => {
  const [searchString, setSearchString] = React.useState<string>("");

  const rawNews: ArticlePreviewInfo[] = React.useMemo(() => {
    return data.allMdx.edges.map((edge) =>
      mapArticleNodeToArticlePreviewInfo(edge.node)
    );
  }, []);

  const filteredNews: ArticlePreviewInfo[] = useLunr(
    searchString.trim().split(" ").map(word => `${word} ${word}*`).join(" "),
    data.localSearchNews?.index,
    data.localSearchNews?.store
  ) as ArticlePreviewInfo[];

  const news = searchString ? filteredNews : rawNews;

  const separatedNewsByCardSize = {
    largeCard: news.slice(0, 1),
    mediumCards: news.slice(1, 5),
    smallCards: news.slice(5),
  };

  return (
    <>
     <MetaData
        title="The Latest News in A2P SMS and 10DLC | Telgorithm"
        description="Stay up to date on the latest news in A2P SMS and 10DLC and learn what it takes to provide the next level of business messaging experiences for your customers."
      />
      <TopLevelPageJSONLDSchemas />
      <GatsbySeoComponent canonicalUrl="https://www.telgorithm.com/news/"/>
      <Layout>
        <NewsContentContainer>
          <NewsPageHeader
            searchString={searchString}
            setSearchString={setSearchString}
          />
          {news.length ? (
            <NewsList>
              <DesktopCardsContainer>
                {separatedNewsByCardSize.largeCard.map((articleItem) => (
                  <ArticleCardItem
                    article={articleItem}
                    key={articleItem.articleId}
                    cardSize={CardSize.L}
                  />
                ))}
                {separatedNewsByCardSize.mediumCards.length > 0 && (
                  <MediumCards>
                    {separatedNewsByCardSize.mediumCards.map((articleItem) => (
                      <ArticleCardItem
                        article={articleItem}
                        key={articleItem.articleId}
                        cardSize={CardSize.M}
                      />
                    ))}
                  </MediumCards>
                )}
                {separatedNewsByCardSize.smallCards.length > 0 && (
                  <SmallCards>
                    {separatedNewsByCardSize.smallCards.map(
                      (articleItem, index) => (
                        <ArticleCardItem
                          article={articleItem}
                          key={articleItem.articleId}
                          cardSize={CardSize.S}
                        />
                      )
                    )}
                  </SmallCards>
                )}
              </DesktopCardsContainer>
              <MobileCardsContainer>
                <div>
                  <MediumCards>
                    {[
                      ...separatedNewsByCardSize.largeCard,
                      ...separatedNewsByCardSize.mediumCards,
                    ].map((articleItem) => (
                      <ArticleCardItem
                        article={articleItem}
                        key={articleItem.articleId}
                        cardSize={CardSize.M}
                      />
                    ))}
                  </MediumCards>
                </div>
                <div>
                  {separatedNewsByCardSize.smallCards.length > 0 && (
                    <SmallCards>
                      {separatedNewsByCardSize.smallCards.map((articleItem) => (
                        <ArticleCardItem
                          article={articleItem}
                          key={articleItem.articleId}
                          cardSize={CardSize.S}
                        />
                      ))}
                    </SmallCards>
                  )}
                </div>
              </MobileCardsContainer>
            </NewsList>
          ) : (
            <NoResultsText>No results for "{searchString}"</NoResultsText>
          )}
        </NewsContentContainer>
      </Layout>
    </>
  );
};

export default NewsPage;

export const query = graphql`
  query NewsPage {
    localSearchNews {
      index
      store
    }
    allMdx(
      filter: { fileAbsolutePath: { regex: "//news//" } }
      sort: { fields: frontmatter___date, order: DESC }
    ) {
      edges {
        node {
          frontmatter {
            articleType
            date(formatString: "MMMM DD, YYYY")
            title
            previewImage {
              childrenImageSharp {
                gatsbyImageData(layout: FIXED)
              }
            }
          }
          id
          slug
        }
      }
    }
  }
`;
